<style lang="scss">
  .middle {
    height: calc(var(--box-pick-height) * 0.75);
    display: flex;
    width: 250px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--background-light-color);
    transition: background 1s;
    position: relative;
    padding-top: 10px;
    overflow: hidden;

    &.blue .pulse  {
      background: linear-gradient(to right, var(--blue-team) 15%, transparent 50%);
      animation: pulsate-blue 3s ease infinite;
    }
    &.red .pulse  {
      background: linear-gradient(to left, var(--red-team) 15%, transparent 50%);
      animation: pulsate-red 3s ease infinite;
    }
  }

  .pulse {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 0;
    transform: translateX(0%);
    transition: transform 1s;
  }

  .timer {
    width: var(--box-width);
    color: inherit;
    font-family: var(--primary-font-family);
    height: calc(var(--box-timer-height) + 10px);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 70px;
    text-align: center;
    color: inherit;
    line-height: 1;
    transition: height 1s, font-size 1s;
  }

  @keyframes pulsate-blue {
    0% {
      transform: translateX(0%);
    }
    50% {
      transform: translateX(-15%);
    }
    100% {
      transform: translateX(0%);
    }
  }
  @keyframes pulsate-red {
    0% {
      transform: translateX(0%);
    }
    50% {
      transform: translateX(15%);
    }
    100% {
      transform: translateX(0%);
    }
  }
</style>

<script lang="ts">
  import { state, bansActive, turn } from "../store"
</script>

<div
  class="middle"
  class:bans-active="{$bansActive}"
  class:blue="{$turn === 'blue'}"
  class:red="{$turn === 'red'}"
>
  <div class:pulse={$turn !== 'neutral'}></div>
  <p class="timer">{$state?.timer ?? 0}</p>
</div>
