  }

  :global(.blue .ban .pulse) {
    background: linear-gradient(to top, var(--blue-team) 85%, transparent 100%);
  }

  :global(.red .ban .pulse) {
    background: linear-gradient(to top, var(--red-team) 85%, transparent 100%);
  }

  .pulse {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    bottom: 0;
    animation: 
      pulsate-pick 3s ease infinite,
      sclae 28s forwards linear;
  }

  .done {
    filter: grayscale(100%);
  }

  .done::before {
    content: '';
