import { writable } from "svelte/store";

export interface Actionable {
  isActive: boolean
}

export class Team implements Actionable {
  bans: Array<Ban> = []
  picks: Array<Pick> = []
  isActive = false
}

export class Ban implements Actionable {
  champion = new Champion()
  isActive = false
}

export class Champion {
  id = 0
  name = ''
  key? = ''
  splashImg = ''
  splashCenteredImg = ''
  loadingImg = ''
  squareImg = ''
  idName = ''
}

export class Pick implements Actionable {
  id: number
  spell1!: Spell
  spell2!: Spell
  champion!: Champion
  isActive = false
  displayName = ''
  constructor(id: number) {
    this.id = id
  }
}

export class Spell {
  id = 0
  key?: string
  icon = ''
}

export interface ConvertedState {
  blueTeam: Team
  redTeam: Team
  timer: number
  timeAfterStart: number
  phase: string
}

export interface Match {
  state: 'NO_MATCH' | 'READY'
  teams: {
    blueTeam?: MatchTeam
    redTeam?: MatchTeam
  }
  bestOf: number
  id?: any
  roundOf: 1 | 2 | 4 | 8 | 16 | 32 | 64 | 128 | 256 | 512 | 1024 | 2048
}

export interface MatchTeam {
  name: string
  tag: string
  score: number
  logo?: string
  color?: string
  standing?: string
  coach?: string
}

export const state = writable<ConvertedState>()
export const match = writable<Match>()
export const isActive = writable(false)
export const bansActive = writable(false)
export const blueColor = writable('var(--text-color-dark)')
export const redColor = writable('var(--text-color)')
export const turn = writable<"blue" | "red" | "neutral">("neutral")