<style lang="scss">
  .team {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .picks {
    width: calc((var(--box-ban-height) / 2.2) * 5);
    height: calc(var(--box-pick-height) * 5 + 50px);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    transition: height 1s;
    margin-top: auto;
    margin-bottom: auto;
    padding: 5px 10px;
    gap: 5px;

    & :global(:nth-child(3)) {
      margin-bottom: 30px;
    }
  }

  .bans {
    height: calc(var(--box-ban-height) + 10px);
    display: flex;
    box-sizing: border-box;
    gap: 10px;
    padding: 0 10px 5px;
    transition: height 1s, padding 1s;
  }

  .blue {
    .teaminfo {
      background: linear-gradient(to right, var(--blue-team-dark) 0%, var(--blue-team) 100%);
    }

    .bans {
      margin-right: auto;

      & :global(:nth-child(3)) {
        margin-right: 30px;
      }
    }

    .score {
      margin-left: auto;
    }
  }

  .red {
    .teaminfo {
      background: linear-gradient(to left, var(--red-team-dark) 0%, var(--red-team) 100%);
    }

    .bans {
      margin-left: auto;
      flex-direction: row-reverse;

      & :global(:nth-child(3)) {
        margin-left: 30px;
      }
    }

    .picks {
      align-self: flex-end;
    }
  }

  .teaminfo {
    height: calc(var(--box-pick-height) * 0.75);
    display: flex;
    align-items: center;
    padding: 7px 25px 0;
    transition: height 1s;
    position: relative;
    overflow: hidden;
  }

  .name {
    display: flex;
    flex-direction: column;
    font-size: 60px;
    color: inherit;
    font-family: var(--primary-font-family);
    justify-content: center;
    line-height: 1;
  }

  .tag {
    font-size: calc(var(--box-pick-height) * 1.15);
    top: -15px;
    right: 65px;
    opacity: 0.5;
    color: inherit;
    text-align: right;
    text-transform: uppercase;
    font-family: var(--primary-font-family);
    justify-content: center;
    line-height: 1;
    position: absolute;
  }

  .red {
    .teaminfo {
      text-align: right;
      flex-direction: row-reverse;
    }

    .tag {
      right: unset;
      left: 65px;
      text-align: left;
    }

    .score {
      margin-right: auto;
    }
  }

  .has-score {
    .name {
      font-size: 50px;
    }
  }

  .score {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .standing {
    font-size: var(--pick-name-height);
    font-family: var(--secondary-font-family);
  }

  .points {
    font-size: 45px;
    color: inherit;
    font-family: var(--primary-font-family);
  }

  .coach {
    font-size: var(--pick-name-height);
    font-family: var(--secondary-font-family);
  }
</style>

<script lang="ts">
  import Ban from "./Ban.svelte"
  import Pick from "./Pick.svelte"
  import { state, bansActive, match, blueColor, redColor } from "../store"

  export let team: 100 | 200

  let picks = []
  $: {
    picks =
      team === 100
        ? $state?.blueTeam.picks ?? [null, null, null, null, null]
        : $state?.redTeam.picks ?? [null, null, null, null, null]
  }

  let bans = []
  $: {
    bans =
      team === 100
        ? $state?.blueTeam.bans ?? [null, null, null, null, null]
        : $state?.redTeam.bans ?? [null, null, null, null, null]

    if (
      ($state?.blueTeam.picks.length > 0 || $state?.redTeam.picks.length > 0) &&
      ($state?.blueTeam.picks.length <= 3 || $state?.redTeam.picks.length <= 3)
    ) {
      bans = [...bans, ...new Array(3 - bans.length).fill(null)]
    } else if (
      ($state?.blueTeam.picks.length > 0 || $state?.redTeam.picks.length > 0) &&
      ($state?.blueTeam.picks.length > 3 || $state?.redTeam.picks.length > 3)
    ) {
      bans = [...bans, ...new Array(5 - bans.length).fill(null)]
    }
  }

  let coach: string

  $ : {
    coach = team === 100 ? $match?.teams?.blueTeam?.coach : $match?.teams?.redTeam?.coach
  }
</script>

<div
  class="team"
  class:blue="{team === 100}"
  class:red="{team === 200}"
  class:bans-active="{$bansActive}"
  style="color: {team === 100 ? $blueColor : $redColor};"
>
  <div class="teaminfo" class:has-score="{$match?.bestOf > 1}">
    <div class="tag">{team === 100 ? $match?.teams?.blueTeam?.tag : $match?.teams?.redTeam?.tag}</div>
    <div class="iofo">
      <div class="name">{team === 100 ? $match?.teams?.blueTeam?.name : $match?.teams?.redTeam?.name}</div>
      <div class="coach">{coach && coach !== '' ? 'Coach: ' + coach : ''}</div>
    </div>
    <div class="score">
      {#if $match?.bestOf > 1}
        <div class="points">
          {team === 100 ? $match?.teams?.blueTeam?.score : $match?.teams?.redTeam?.score}
        </div>
      {/if}
      <div class="standing">
        {team === 100 ? $match?.teams?.blueTeam?.standing ?? "" : $match?.teams?.redTeam?.standing ?? ""}
      </div>
    </div>
  </div>
  <div class="picks">
    {#each picks as pick}
      <Pick pick="{pick}" />
    {/each}
  </div>
  <div class="bans">
    {#each bans as ban}
      <Ban ban="{ban}" />
    {/each}
  </div>
</div>
