<style lang="scss">
  .pick {
    position: relative;
    flex-grow: 1;
    background-color: var(--background-light-color);
    border: solid var(--border-between-color) 1px;
    border-bottom: none;
    align-items: flex-end;
    display: flex;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center top;
    transition: height 1s, opacity .5s;
  }

  .active {
    height: calc(var(--box-pick-height) * 1.5);
    opacity: 1;

    .name {
      height: calc(2 * var(--pick-name-height)) !important;
      margin-top: calc(-1 * var(--pick-name-height));
      line-height: calc(2 * var(--pick-name-height));
    }

    &.done::before {
      height: calc(var(--box-pick-height) * 1.15 - 2 * var(--pick-name-height));
    }

    .name p::after {
      top: 17px;
    }
  }

  :global(.blue .pick:nth-child(4)::before) {
    background: linear-gradient(to right, var(--blue-team-dark) 0%, var(--blue-team) 100%);
  }
  :global(.red .pick:nth-child(4)::before) {
    background: linear-gradient(to left, var(--red-team-dark) 0%, var(--red-team) 100%);
  }

  :global(.blue .pick.active .name) {
    color: inherit;
    background-color: var(--blue-team);
  }
  :global(.red .pick.active .name) {
    color: inherit;
    background-color: var(--red-team);
  }

  :global(.blue .pick .pulse) {
    background: linear-gradient(to right, var(--blue-team) 85%, transparent 100%);
  }

  :global(.red .pick .pulse) {
    left: unset !important;
    right: 0;
    background: linear-gradient(to left, var(--red-team) 85%, transparent 100%);
  }

  .pick:nth-child(4)::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 20px;
    left: 0;
    top: -29px;
    z-index: 10;
  }

  .name p::after {
    content: '';
    position: absolute;
    height: 20px;
    width: 20px;
    top: 3px;
    left: -35px;
    -webkit-mask: url(../img/top.svg) no-repeat;
    mask: url(../img/top.svg) no-repeat;
    -webkit-mask-size: contain;
    mask-size: contain;
    background: var(--text-color);
    transition: top 1s;
  }

  :global(.red .name p::after) {
    left: unset !important;
    right: -35px;
  }

  .pick:nth-child(2) .name p:after {
    -webkit-mask: url(../img/jungle.svg) no-repeat;
    mask: url(../img/jungle.svg) no-repeat;
  }

  .pick:nth-child(3) .name p:after {
    -webkit-mask: url(../img/mid.svg) no-repeat;
    mask: url(../img/mid.svg) no-repeat;
  }

  .pick:nth-child(4) .name p:after {
    -webkit-mask: url(../img/bottom.svg) no-repeat;
    mask: url(../img/bottom.svg) no-repeat;
  }

  .pick:nth-child(5) .name p:after {
    -webkit-mask: url(../img/support.svg) no-repeat;
    mask: url(../img/support.svg) no-repeat;
  }

  .name {
    text-align: left;
    width: 100%;
    transition: height 1s, background-color 1s, color 0.3s;
    height: var(--pick-name-height);
    color: inherit;
    background-color: transparent;
    color: var(--text-color);
    font-size: var(--pick-name-height);
    line-height: var(--pick-name-height);
    font-family: var(--secondary-font-family);
    position: relative;
    z-index: 10;
    display: flex;
    align-items: center;
    padding: 0 10px;

    p {
      margin-left: 35px;
      position: relative;
    }

    .summoners {
      width: calc(var(--pick-name-height) * 2 + 5px);
      height: calc(var(--pick-name-height) * 20px);
      display: flex;
      gap: 5px;
      opacity: 0;
      transition: opacity 1s;
      padding: 0 0 10px;

      img {
        width: var(--pick-name-height);
        height: var(--pick-name-height);
      }

      &.show {
        opacity: 1;
      }
    }
  }

  .pulse {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    animation: pulsate-pick 3s ease infinite, sclae 28s forwards linear;
  }

  .done {
    opacity: 1;
  }

  .done::before {
    content: '';
    width: 100%;
    height: calc(100% - var(--pick-name-height));
    position: absolute;
    opacity: 0;
    top: 0;
    animation: done 1.5s forwards ease-out;
    transition: height 1s;
  }

  :global(.red .pick .name) {
    text-align: right;
  }

  :global(.red .pick .name) {
    flex-direction: row-reverse;

    p {
      margin-left: unset;
      margin-right: 35px;
    }
  }
  :global(.blue .pick .summoners) {
    margin-left: auto;
  }
  :global(.red .pick .summoners) {
    margin-right: auto;
  }

  :global(.blue .pick.done::before) {
    box-shadow: inset 0 0 25px var(--blue-team);
  }

  :global(.red .pick.done::before) {
    box-shadow: inset 0 0 25px var(--red-team);
  }

  @keyframes pulsate-pick {
    0% {
      opacity: 0.3;
    }
    50% {
      opacity: 0.1;
    }
    100% {
      opacity: 0.3;
    }
  }

  @keyframes sclae {
    0% {
      width: 100%;
    }
    100% {
      width: 0%;
    }
  }

  @keyframes done {
    0% {
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
</style>

<script lang="ts">
  import { Pick, state } from "../store"

  export let pick: Pick | null
  let done = pick?.isActive && pick.champion.splashCenteredImg
</script>

<div class="pick" class:active={pick?.isActive} style="background-image: url({pick?.champion.splashCenteredImg});" class:done >
  <div class:pulse={pick?.isActive}></div>
  <div class="name">
    <p>{pick?.displayName}</p>
    <div class="summoners" class:show={$state?.phase === 'GAME_STARTING'}>
      <img src={pick?.spell1.icon} alt="">
      <img src={pick?.spell2.icon} alt="">
    </div>
  </div>
</div>